@media screen and (max-width: 1540px) {
  .sectionPlaces_img {
    left: -150px;
  }
}
@media screen and (max-width: 1250px) {
  .sectionPlaces_img {
    left: -200px;
  }
}
@media screen and (max-width: 1120px) {
  .sectionInner {
    padding: 0 40px;
    width: 100%;
  }

  .sectionHeader_btnWrapper {
    width: 300px;
  }

  .sectionHeader_btnWrapper-btn {
    width: 145px;
  }

  .sectionWorkComfort .sectionInner {
    width: 100%;
    height: 608px;
  }

  .sectionWorkComfort_text,
.sectionWorkComfort_img {
    width: 48%;
    height: 100%;
  }

  .sectionWorkComfort_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .sectionLocation_cards {
    width: 100%;
    justify-content: space-between;
  }

  .sectionLocation_cards-item {
    width: calc(33.3333333333% - 20px);
    margin: 0;
  }

  .sectionForm_form {
    width: 100%;
    justify-content: space-between;
  }

  .sectionForm_form-input,
.sectionForm_form-submit {
    width: calc(33.3333333333% - 20px);
    margin: 0;
  }

  .sectionWhyUs .sectionInner {
    width: 100%;
    height: 1230px;
  }

  .sectionWhyUs_list,
.sectionWhyUs_img {
    width: 48%;
    height: 100%;
  }

  .sectionWhyUs_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .sectionWhyUs_btn {
    width: 100%;
  }

  .sectionPlaces_logo {
    width: 30%;
  }

  .sectionTarifs_table th:first-child,
.sectionTarifs_table td:first-child {
    width: 250px;
  }
}
@media screen and (max-width: 992px) {
  html {
    font-size: 14px;
  }

  .sectionHeader {
    height: 125px;
  }

  .sectionHeader .sectionInner {
    padding-top: 20px;
    align-items: flex-start;
  }

  .sectionHeader_lang {
    height: 40px;
  }

  .sectionHeader_contacts {
    position: absolute;
    bottom: 20px;
    width: calc(100% - 80px);
  }

  .sectionHeader_contacts-socialLogo {
    position: absolute;
    right: 0;
  }

  .whatsApp {
    right: 46px;
  }

  .sectionBanner {
    margin-top: 125px;
  }

  .sectionTarifs_table td:first-child,
.sectionTarifs_table th:first-child {
    width: auto;
    padding: 5px 0 5px 15px;
  }

  .sectionTarifs_table th {
    min-width: auto;
  }

  .sectionPlaces_img {
    height: 390px;
  }

  .sectionPlaces_item {
    height: 114.3333333333px;
    padding: 0 0 0 42%;
  }

  .sectionForm_form-input,
.sectionForm_form-submit {
    height: 50px;
  }

  .sectionLocation_cards-item {
    width: calc(33.3333333333% - 5px);
  }

  .sectionLocation_cards-itemImg {
    margin-right: 10px;
    width: 37px;
  }
}
@media screen and (max-width: 918px) {
  .sectionMap .sectionMap_map {
    width: 100%;
    overflow-y: scroll;
  }
}
@media screen and (max-width: 768px) {
  .sectionHeader_burger .burger-menu_nav {
    height: 450px;
    top: -480px;
  }

  .burger-menu .burger-menu_nav .sectionHeader_lang {
    margin: 20px 0;
    width: 100%;
    display: flex;
  }
  .burger-menu .burger-menu_nav .socialLogosWrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .burger-menu .burger-menu_nav .socialLogosWrapper .whatsApp,
.burger-menu .burger-menu_nav .socialLogosWrapper .telegram {
    position: static;
    width: 26px;
    height: 26px;
  }
  .burger-menu .burger-menu_nav .sectionHeader_btnWrapper {
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    margin: 20px 0;
    display: flex;
  }
  .burger-menu .burger-menu_nav .sectionHeader_btnWrapper .sectionHeader_btnWrapper-btn {
    margin: 0;
    width: calc(50% - 10px);
    font-size: 0.8rem;
  }
  .burger-menu .burger-menu_nav .sectionHeader_contacts-phone {
    font-family: "Numans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.25rem;
    display: block;
  }

  #relative {
    overflow: scroll;
    position: relative;
    width: 100%;
  }

  .sectionTarifs_table {
    border-collapse: separate;
    border-spacing: 0;
  }

  .sectionTarifs_table td:first-child,
.sectionTarifs_table th:first-child {
    position: sticky;
    left: 0;
    z-index: 99;
  }

  .sectionTarifs_table tr:nth-child(2n+1), .sectionTarifs_table tr:nth-child(2n+1) td {
    background-color: #f2f2f2;
  }

  .sectionTarifs_table td {
    background-color: #fff;
  }

  .sectionHeader_btnWrapper,
.sectionHeader_lang,
.sectionHeader_contacts {
    display: none;
  }

  .sectionHeader {
    height: 80px;
  }

  .sectionBanner {
    margin-top: 80px;
  }

  .sectionHeader_btnWrapper-btn:first-child {
    margin: 0 0 10px 0;
  }

  .sectionBanner {
    height: auto;
  }

  .sectionBanner.workspace .sectionBanner_text {
    width: 100%;
  }

  .sectionBanner .sectionInner {
    flex-direction: column;
    padding: 20px;
  }

  .sectionBanner_text {
    margin: 0 0 30px 0;
  }

  .sectionBanner_form {
    width: 100%;
  }

  .sectionWorkComfort .sectionInner {
    height: auto;
    flex-direction: column;
  }

  .sectionWorkComfort_img,
.sectionWorkComfort_text {
    width: 100%;
  }

  .sectionWorkspace .sectionInner {
    padding: 20px;
  }

  .sectionWhyUs .sectionInner {
    height: auto;
  }

  .sectionWhyUsFirstRow,
.sectionWhyUsSecondRow {
    flex-direction: column;
  }

  .sectionWhyUs_img,
.sectionWhyUs_list {
    width: 100%;
  }

  .sectionWhyUs_img {
    order: 1;
    margin: 0 0 10px 0;
  }

  .sectionWhyUs_list {
    order: 2;
  }

  .sectionWhyUsFirstRow {
    margin: 0 0 40px 0;
  }

  .sectionPlaces_img {
    display: none;
  }

  .sectionPlaces_logo {
    width: calc(100% - 40px);
  }

  .sectionPlaces_item {
    padding: 0 0 0 20px;
  }

  .sectionLocation_cards {
    flex-direction: column;
  }

  .sectionLocation_cards-item {
    width: 280px;
    margin: 0 0 20px 0;
    justify-content: flex-start;
  }
  .sectionLocation_cards-item:last-child {
    margin: 0;
  }

  .sectionLocation_cards-itemImg {
    margin: 0 20px 0 0;
  }

  .sectionForm_form {
    flex-direction: column;
  }

  .sectionForm_form-input,
.sectionForm_form-submit {
    width: 100%;
  }

  .sectionForm_form-input {
    margin: 0 0 20px 0;
  }

  .sectionPlaces_item span {
    min-width: auto;
  }
}
@media screen and (max-width: 568px) {
  .sectionHeader_burger .burger-menu_nav {
    width: 280px;
  }

  .sectionHeader_Logo {
    width: 180px;
  }

  .sectionWorkComfort_text-title,
.sectionTarifs_title,
.sectionWhyUs_title,
.sectionLocation_title,
.sectionForm_title,
.sectionMap_title {
    font-size: 2.8rem;
  }

  .sectionPlaces_item {
    font-size: 2.5rem;
  }
}